.yield-section{
    background: url('../assets/images/SolutionCardThree.png');
    height: 600px;
    background-size: cover;
    background-position: top right;
    background-repeat: no-repeat;
    display: flex;
    align-items: flex-end;
    padding: 40px;
    border-radius: 24px;

}

.yield-section .cardContent{
    height: fit-content;
}



@media(max-width:800px){
    .yield-section{
      padding: 0;
    }
  }