/* Swap Section Styling */
.fuse-section {
    padding-top: 20px;
    padding-right: 44px;
    padding-left: 44px;
    background: radial-gradient(circle at top left, rgba(23, 23, 23, 1) 0%, rgba(255, 255, 255, 0.05) 50%, rgba(23, 23, 23, 1) 100%);

    color: #fff;
    border-radius: 24px;
    gap: 2rem;
    min-height: 650px;
    position: relative;

  }

  .cardContent h3{
    font-family: 'Outfit', sans-serif;
    font-size: 44px;
    line-height: 100%;
    letter-spacing: 2%;
    font-weight: normal;
    text-align: left;
    margin-top: 0;
    margin-bottom: 24px;
  }

  .fuse-section .cardContent h3{
    max-width: 400px;
  }
    

  .cardContent p{
    font-family: 'Outfit', sans-serif;
    font-size:20px;
    font-weight: 300;
    line-height: 28px;
    text-align: left;
    color: rgba(255, 255, 255, 0.5);
    margin: 0;
  }

  .fuse-section .cardContent p{
    max-width: 80%;
  }

  .imageHolder{
  }

  .imageHolder img.main{
    max-height:30vw;
    max-width: 100%;
    border-top-left-radius: 22px;
    border-top-right-radius: 22px;
    
  }


  @media(max-width:800px){
    .fuse-section{
      padding: 0;
    }
    .imageHolder img.main{
      max-height: -webkit-fill-available;
      margin: 10px;
    }
    .fuse-section .cardContent p{
      max-width: 100%;
    }
  }